import FlexBox from "../../layout/FlexBox";
import Icon from "../../layout/Icon";

const styleBySize = {
  S: "px-[8px] py-[4px]",
  M: "px-[8px] py-[4px] gap-[2px]",
  L: "px-[16px] py-[6px] gap-[4px]",
};

const textStyleBySize = {
  S: "B5-regular",
  M: "B5-regular",
  L: "B4-regular",
};

const styleByActive = {
  active: "bg-Black border-Black",
  inactive: "bg-white border-Gray2",
};

const textStyleByActive = {
  active: "text-White",
  inactive: "text-Gray4",
};

const pixelSize = {
  S: 0,
  M: 12,
  L: 16,
};

interface Props {
  size: "S" | "M" | "L";
  text: string;
  src?: string;
  className?: string;
  inactive?: boolean;
  onChipClick?: () => void;
}

/**
 * TextChip    .
 * @param size   (S | M | L)
 * @param text  
 * @param src     . size L  M  .     .
 * @param className  
 * @param inactive   . true   ,   .
 * @param onChipClick     
 * @returns
 * @example <TextChip size="L" src="/icon/sample.svg" text="" />
 */
export default function TextChip({
  size,
  text,
  src,
  className,
  inactive,
  onChipClick,
}: Props) {
  const active = inactive ? "inactive" : "active";
  return (
    <button type="button" className="shrink-0" onClick={onChipClick}>
      <FlexBox
        className={`w-fit rounded-full border border-1 ${className} ${styleBySize[size]} ${styleByActive[active]}`}
      >
        {(size === "L" || size === "M") && src && (
          <Icon src={src} sz={pixelSize[size]} />
        )}
        <div
          className={`${textStyleBySize[size]} ${textStyleByActive[active]}`}
        >
          {text}
        </div>
      </FlexBox>
    </button>
  );
}
