import { useState } from "react";
import FlexBox from "../../layout/FlexBox";
import Icon from "../../layout/Icon";

interface TextFieldProps {
  value?: string;
  setValue?: React.Dispatch<React.SetStateAction<string>>;
  label?: string;
  assistText?: string;
  className?: string;
  isError?: boolean;
  button?: React.ReactNode;
}

/**
 * TextField      .
 * useState  value setValue  .
 * @param value -    (state)
 * @param setValue -      (setState)
 * @param label -   
 * @param assistText -    
 * @param className -   ,   B3-regular 
 * @param isError -    , true   .
 * @param button -     , ReactNode .
 * @param props - html input   .
 * @returns
 */
export default function TextField({
  value,
  setValue,
  label,
  assistText,
  className,
  isError,
  button,
  ...props
}: TextFieldProps & JSX.IntrinsicElements["input"]) {
  const [focus, setFocus] = useState<boolean>(false);

  const onClickCancel = () => {
    setValue && setValue("");
  };

  const handleChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    setValue && setValue(e.target.value);
    props.onChange && props.onChange(e);
  };

  const handleFocus = (e: React.FocusEvent<HTMLInputElement>) => {
    setFocus(true);
    props.onFocus && props.onFocus(e);
  };

  const handleBlur = (e: React.FocusEvent<HTMLInputElement>) => {
    setFocus(false);
    props.onBlur && props.onBlur(e);
  };

  return (
    <FlexBox direction="col" className="w-full gap-1">
      {label && (
        <label
          className="B4-regular leading-[16px] w-full text-Gray6"
          htmlFor={props.id}
        >
          {label}
        </label>
      )}
      <FlexBox
        className={`w-full justify-between h-[56px] border-b-2 ${
          isError ? "border-Red" : focus ? "border-Black" : "border-Gray3"
        }`}
      >
        <input
          {...props}
          className={`w-full h-full bg-transparent outline-none ${
            isError ? "text-Red" : focus ? "text-black" : "text-Gray5"
          } ${className || "B3-regular"} placeholder:text-Gray3`}
          value={value}
          onChange={handleChange}
          onFocus={handleFocus}
          onBlur={handleBlur}
        />
        {button}
        {!button && focus && (
          <Icon
            src={`/icon/textfields/close_${isError ? "red" : "black"}.png`}
            alt="x"
            sz={20}
            className="cursor-pointer"
            onClick={onClickCancel}
          />
        )}
      </FlexBox>
      {assistText && (
        <p
          className={`w-full C2 leading-[16px] ${
            isError ? "text-Red" : focus ? "text-Gray7" : "text-Gray5"
          }`}
        >
          {assistText}
        </p>
      )}
    </FlexBox>
  );
}
